@import "../../vars.css";

.pricingTables {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    margin-top: 0px;
    margin-left: 10%;
    margin-right: 10%;
    font-family: var(--font-outfit);
    color: var(--color-text);
    border-radius: 15px;
    gap: 45px;
    z-index: 1;
    width: 80%;

       
}

.pricingText {
    text-align: center;
}

.pricingText h3 {
    font-size: 2.8rem;
}

.pricingText p {
    font-size: 1.5rem;
}


.pricingTitle {
   display: flex;
   position: relative;
    text-align: center;
    justify-content: center;
    font-size: 4rem;
    color: var(--color-accent);
    
}

.pricingTable1 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-top: 2%;
    font-family: var(--font-outfit);
    color: var(--color-text);
    background: var(--color-primary);
    border-radius: 15px;
    z-index: 1;
    box-shadow: 2px 6px 46px rgba(202, 214, 241, 1);
    align-items: center;
    justify-content: center;
    width: 40%;
    padding: 2% 1%;
    
    
}

.pricingTable1 img {
   width: 70%; 
     
}

.pricingTable1 h2 {
    text-align: center;
    font-size: 3rem;
    color: #16194F;
    font-family: var(--font-robot);
     
 }

 .pricingTable1 h3 {
    text-align: center;
    font-size: 2rem;
    color: #16194F;
    font-family: var(--font-robot);
     
 }
 
 .pricingUl {
    padding-top: 20px;
    list-style: none;
    line-height: 2.5rem;
    color: #16194F;
    font-family: var(--font-robot);
    padding-left: 0;
 }

 .pricingUl li:before {
    color: var(--color-secondary);
    font-weight: 600;
    content: ' ✓ ';
  }

.active {
    color: black;
 }

 .inactive {
    color: #B7B7B7;
 }

 .pricingUl .inactive:before {
    color: #B7B7B7;
    
  }


 .pricingButton {
    text-decoration: none;
    background-color: none;
    color: var(--color-text);
    font-family: var(--font-robot);
    border-radius: 100px;
    font-size: 15px;
    font-weight: 600;
    padding: 17px 56px;
    border: none;
    box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.25);


   background-image: linear-gradient(90deg, rgba(85, 77, 222, 1) 0%, rgba(255, 255, 255, 0.4) 100%);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: 0.4s;
    z-index: 4;

    margin-bottom: 30px;
 }


.pricingButton:hover {
    background-size: 100% 100%;
    cursor: pointer;
}

.pricingButton:active {
    background-color: rgba(25, 55, 109, 1);
    
}
.cardBtn {
    width: 2px;
}

@media screen and (max-width: 830px) {
    .pricingContainer {
        padding: 2%;
    }

    .pricingTables {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        width: 100%;
    
           
    }

    .pricingTable1 {
        width: 80%;
    }

}