@import "../../vars.css";

.aboutSecondContainer {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    margin-left: 5%;
    margin-right: 5%;
    font-family: var(--font-robot);
    color: var(--color-text);
    z-index: 1;
    
}

.aboutSecond {
    padding-left: 10%;
}


.aboutSecondTitle {
    
    color: var(--color-secondary);
    text-transform: capitalize;
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 1px;   
    width: 70%;
 
    
}

.aboutSecondContent {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 25px;
    width: 100%;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    padding-bottom: 5%;
  
}

.aboutSecondContainer img {
    margin-top: 5%;
    width: 50%;
    margin-left: 5%;
   
    
}

.aboutSecondBtn {
    text-decoration: none;
    background-color: var(--color-accent-extra);
    color: var(--color-primary);
    font-family: var(--font-robot);
    border-radius: 100px;
    font-size: 25px;
    font-weight: 600;
    padding: 17px 56px;
    border: none;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);


   background-image: linear-gradient(90deg, rgba(85, 77, 222, 1) 0%, rgba(255, 255, 255, 0.4) 100%);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: 0.4s;
    z-index: 4;
    
}

.aboutSecondBtn:hover {
    background-size: 100% 100%;
    cursor: pointer;
}

.aboutSecondBtn:active {
    background-color: rgba(25, 55, 109, 1);
    
}

@media screen and (max-width: 830px) {
    .aboutSecondContainer {
        margin: 0;
        flex-direction: column-reverse;
        padding-right: 5%;

    }

    .aboutSecondTitle {
    text-align: center;
    width: 100%;   
   }

    
    .aboutSecondContent {
     flex-direction: column;
    width: 100%;

    }

    .aboutSecondContainer img {
    width: 80%;
    margin: 0 auto;
    padding: 5% 15%;


    }

    .aboutSecondBtn {
        position: relative;
        display: flex;
        margin: 0 auto;
        
       
    }

}