@import "../../vars.css";

.footerContainer {
    position: relative;
    display: flex;
    align-items: center;
    align-content: center;
    flex-direction: row;
   
    color: var(--color-primary);
    font-family: var(--font-robot);
    padding: 1% 10%;
    gap: 30%;
  
    background: rgba(202, 214, 241, 1);
        
 
}

.footerContainer img{
   padding-left: 10%;
        
 }

 .footerList {
   flex: 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    list-style: none;
    max-height: 120px;
    gap: 0%;
    line-height: 150%;
    
    
    
 }

 .footerList li {
   
    padding-top: 1rem;
    padding-left: 50%;
   
 }

 .footerinnerbox {
    padding: 2%;
    width: 100%;
   
    
    
 }

 .footerContainer a {
   
    text-decoration:  none;
    color: var(--color-text);
    font-weight: 600;
    

    
 }

 .footerContainer a:hover {
    color: var(--color-accent-extra);
 }

 @media screen and (max-width: 830px) {

    .footerContainer {
       
        padding: 0;
       gap: 0;
       
       
    
        
 
}

.footerContainer a {
    padding-right: 20%;
}

.footerList {
    width: 100%;
    height: 10rem;
    gap: 0;
    
    flex-direction: column;
    margin: 0 auto;
    overflow: hidden;
  
     
 }

 .footerList li {
    padding: 5px;
    width: 90px;
 }
 .footerContainer img{
 display: none;
        
 }
 
 }

 @media screen and (max-width: 1080px) {
   .footerContainer {
      padding: 1% 15%;
      gap: 0%;

   }
 }