@import "../../vars.css";

.aboutContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 129px;
    margin-left: 15%;
    margin-right: 10%;
    font-family: var(--font-robot);
    color: var(--color-text);

    padding: 0;
    z-index: 1;
    
}

.aboutTitle {
    
    color: var(--color-secondary);
    text-transform: capitalize;
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 1px;   
    width: 80%;
    
}

.aboutContent {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 70%;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
}

.aboutContainer img {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 50%;
}

@media screen and (max-width: 830px) {
    .aboutContainer {
        margin: 0;
        flex-direction: column;
        padding: 5% 10%;

    }

    .aboutTitle {
    text-align: center;
    width: 100%;   
 }

    
    .aboutContent {
        flex-direction: column;
        width: 100%;

    }

    .aboutContainer img {
    width: 90%;

    }

}

