.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
    
}

.slider {
    position: absolute;
    cursor: pointer;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background-color: #ccc;
    border-radius: 2rem;
    
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: rgba(42,61,103);
    transition: 0.4s;
    border-radius: 100%;
}

input:checked + .slider {
    background: image();
}

input:checked + .slider:before {
    transform: translateX(26px);
    background: rgba(137,19,83);
}