@import "../../vars.css";

.heroContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 0px;
    z-index: 0;
    padding: 0% 5% 10% 10%;
    font-family: var(--font-robot);
    overflow: hidden; 

                
}



.heroContent {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: var(--color-text);
    z-index: 3;
    
}



.herotitle {
    position: relative;
    font-size: 6rem;
    font-weight: 900;
    margin-bottom: 33px;
    font-family: var(--font-robot);
    width: 75%;
    text-transform: capitalize;
    z-index: 4;
   color: var(--color-primary);
    

}

.heroImg{
    width: 60%;
}


.herodescription {
    position: relative;
    font-size: 2.5rem;
    color: var(--color-primary);
    font-family: var(--font-robot);
    margin-bottom: 52px;
    width: 70%;
    z-index: 4;
    
    

    
}

.herocontactBtn {
    text-decoration: none;
    background-color: var(--color-accent-extra);
    color: var(--color-primary);
    font-family: var(--font-robot);
    border-radius: 100px;
    font-size: 25px;
    font-weight: 600;
    padding: 17px 56px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);


   background-image: linear-gradient(90deg, rgba(85, 77, 222, 1) 0%, rgba(255, 255, 255, 0.4) 100%);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: 0.4s;
    z-index: 4;
    
}

.herocontactBtn:hover {
    background-size: 100% 100%;
}

.herocontactBtn:active {
    background-color: rgba(25, 55, 109, 1);
    
}

.topBlur {
    position: absolute;
    width: 25vh;
    height: 25vw;
    min-height: 350px;
    min-width: 350px;
    top: 200px;
    left: -10vw;
    border-radius: 764px;
    background: #9E4E77;
    filter: blur(100px);
    z-index: 1;

}

.bottomBlur {
    display: none;
    position: absolute;
    width: 70vh;
    height: 50vw;
    min-height: 350px;
    min-width: 250px;
    top: 15vh;
    right: -25vw;
    border-radius: 764px;
    background: rgba(25, 55, 109, 0.7);
    filter: blur(100px);
    z-index: 1;

}


@media screen and (max-width: 830px) {
    .heroContainer {
        width: 100%;
        flex-direction: column;
        padding: 5%;
        margin: 10% 5%;
        

    }

    .herotitle {
        width: 90%;
        font-size: 4.5rem;
    }

    .heroContainer img {
      display: none;

    }

    .topBlur {
        display: none;
    }

    .bottomBlur {
        display: none;
    }
    
    .herocontactBtn {
        position: relative;
        display: flex;
        margin-bottom: 5%;
        font-size: 2rem;
        


    }

    .herodescription {
        width: 90%;
        font-size: 2.3rem;
 
        
    }

}

@media screen and (max-width: 1080px) {
    .topBlur {
       
    
    }
    
    .bottomBlur {
      display: none;
    
    }
}