@import "../../vars.css";

.aboutThirdContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 150px;
    margin-left: 10%;
    margin-right: 10%;
    padding-bottom: 10%;
    font-family: var(--font-robot);
    color: var(--color-text);
    z-index: 1;

    
    background-image: url("../../assets/AboutThird/bg.png");
    background-repeat: no-repeat;
    background-position: top left;

    
}

.aboutThird {
    padding-left: 5%;
    
 
}


.aboutThirdTitle {
    
    color: var(--color-secondary);
    text-transform: capitalize;
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 1px;   
    width: 80%;
    
 
    
}

.aboutThirdContent {
    width: 100%;
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;

   
  
}

.aboutThirdContainer img {
    margin-top: 0;
    width: 50%;
    
    
}

.aboutThirdContainer .chat {
    background-image: url("../../assets/AboutThird/bg.png");
    background-repeat: no-repeat;
    background-position: bottom right;
    width: 50%;
}

.aboutThirdContainer .friends {
   width: 15rem;
}


@media screen and (max-width: 830px) {
    .aboutThirdContainer {
        margin: 0;
        flex-direction: column;
        padding-right: 5%;

    }

    .aboutThirdTitle {
    text-align: center;
    width: 100%;   
   }

    
    .aboutThirdContent {
     flex-direction: column;
    width: 100%;

    }

    .aboutThirdContainer img {
    width: 80%;
    margin: 0 auto;
    padding: 5% 10%;


    }

    .aboutThirdContainer .friends {
        margin: 0 5%;
        width: 20rem;
     }

}