@import "../../vars.css";

.navbar {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 10px;
    z-index: 2;
    margin: 0 5%;
  
}

.menu {
    display: flex;
   
    
}

.menuItems {
    display: flex;
    gap: 47px;
    list-style: none;
 
}

.menuItems a {
    font-size: 25px;
    color: var(--color-primary);
    text-decoration: none;
    font-family: var(--font-robot);
    font-weight:500;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.6);


}

.menuItems a:hover {
    color:  var(--color-accent-extra);
    text-shadow: 2px 2px 2px rgba(255, 255, 255, 0.5);
    font-weight: 600;

}

.menuBtn {
    display: none;
}

.signup {
    position: relative;
    padding: 0px 56px;
    margin-left: 100px;
    font-size: 20px;
    font-weight: 700;
    background: var(--color-accent);
    border: none;
    border-radius: 100px;
    color: aliceblue;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);

    
   background-image: linear-gradient(90deg, rgba(85, 77, 222, 1) 0%, rgba(255, 255, 255, 0.4) 100%);
   background-size: 0 100%;
   background-repeat: no-repeat;
   transition: 0.4s;
   z-index: 4;
   display: none;
   
}

.signup:hover {
    cursor: pointer;
    background-size: 100% 100%;
}

.signup:active {
    background:  rgba(25, 55, 109, 1);
}


@media screen and (max-width: 830px) {
    .navbar {
        flex-direction: column;
        align-items: flex-start;
        }


.menu {
    position: absolute;
    right: 0px;
    margin-right: 10%;
    flex-direction: column;
    align-items: flex-end;
    gap: 11px;
    z-index: 3;
}

.menuBtn {
    display: block;
    cursor: pointer;
   
    }

    .menuItems {
        display: none;
        flex-direction: column;
        align-items: center;
        gap: 13px;
        border-radius: 10px;
        background: rgb(25, 55, 109);
        background: linear-gradient(0deg, rgba(25,55,109,0.2) 0%, rgba(25,55,109, 1) 100%);
        padding: 24px 33px;
        box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.25);
    }

    .menuOpen {
        display: flex;
        z-index: 1;
    }

    .menuItems a:hover {
        text-shadow: 2px 2px 1px rgba(0, 0, 0, 0.6);
        
    
    }
};