@import "../../vars.css";

.faqContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
 
    margin-top: 79px;
    margin-left: 10%;
    margin-right: 10%;
    padding-bottom: 10%;
    

}

.faqContent {
    width: 80%;
    
    

}



.item {
    border-radius: 20px;
    background: #FFFFFF;
    margin-bottom: 10px;
    padding: 1rem 1.2rem 1.5rem;
    box-shadow: 2px 6px 46px rgba(202, 214, 241, 1);
    font-family: var(--font-robot);
    font-size: 1.2rem;
    
    
}
.item h2 {
    font-size: 1.4rem;
}


.content {
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s cubic-bezier(0,1,0,1);
    
    

}

.content.show {
    height: auto;
    max-height: 9999px;
    transition: all 0.5s cubic-bezier(1,0,1,0);
        
}

.title {
    color: var(--color-text);
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    
      
    
}

.faqTitle {
    display: flex;
    position: relative;
     text-align: center;
     justify-content: center;
     font-size: 4rem;
     color: var(--color-accent);
}

.faqContainer p {
    font-size: 1.5rem;
        
}

span {
    font-size: 2rem;
    color: #6A6D9E;
}

@media screen and (max-width: 830px) {

    .faqContent {
    width: 100%;
    
    

}

}